	module.exports = {"data":{"site":{"siteMetadata":{"title":"Academic Achievement Report SY 2016-2017","description":"","author":{"name":"Tyler Collins"}}},"markdownRemark":{"html":"<h2>Notice to Persons with Disabilities</h2>\n<p>The CNMI Public School System is committed to providing equal access to educational and professional opportunities to all members of our community.  In accordance with the CNMI Public School System’s obligations under Title II of the Americans with Disabilities Act and Section 504 of the Rehabilitation Act, the CNMI PSS District Website has been re-designed to conform to WCAG 2.0 Level AA and WAI-ARIA 1.0 accessibility guidelines.  The changes to the website include improved compatibility with assistive technology, easier keyboard navigation, and the consistent use of text equivalents for audio/visual content, among other changes.</p>\n<p>If you have questions, comments, or concerns regarding the accessibility or usability of any part of the CNMI Public School System website or the websites of any individual public schools or public school programs in the CNMI, please contact the CNMI PSS Webmaster, Tyler Collins, via email at webmaster@cnmipss.org or by telephone at the CNMI PSS Office of Administrative Services (670) 664-3820.</p>\n<p>If you would like to file a formal grievance against the CNMI PSS pursuant to the requirements of Section 504 and Title II, you may submit such grievance to the Legal Counsel Office Attorney Ryan Meyerhoff by telephone (670) 237-3080 or email ryan.meyerhoff@cnmipss.org.</p>","excerpt":"Notice to Persons with Disabilities The CNMI Public School System is committed to providing equal access to educational and professional…","frontmatter":{"title":"AccessibilityNotice"}}},"pathContext":{"slug":"/AccessibilityNotice/"}}

